<template>
    <view class="integral_construction_points_info_page">
        <!-- tabbar -->
        <cu-custom><block slot="content">建设分详情</block></cu-custom>
        <!-- 头部 -->
        <view class="things_width_full flex justify-start align-start integral_construction_points_info_page_top">
            <!-- 余额 -->
            <view class="flex justify-center align-start balance">
                <view class="balance_text">建设分</view>
                <view class="balance_value">{{userAmount.build_points}}</view>
            </view>
            <!-- 积分规则 -->
            <view class="rule_info">
                <view class="rule_text">积分规则</view>
                <view class="flex justify-end align-center btn_box">
                    <button class="cu-btn" :disabled="buttonType" @tap="linkToHang">交易</button>
                </view>
            </view>
        </view>
        <!-- 列表 -->
        <view class="construction_points_info_list">
            <!-- 导航栏 -->
            <scroll-view scroll-x class="bg-white nav">
                <view class="flex text-center navBox">
                    <view class="cu-item flex-sub" :class="item.id==tabCur?'things_active_color':''" v-for="(item,index) in tabData" :key="index" @tap="tabSelect" :data-id="item.id">
                        {{item.name}}
                    </view>
                </view>
            </scroll-view>
            <!-- 建设分列表 -->
            <scroll-view v-if="tabCur == 1 && constructionList[0].list.length > 0" class="construction_points_list" scroll-y @scrolltolower="getMoreData">
                <view class="flex justify-between align-center construction_points_item" v-for="(item,index) in constructionList[0].list" :key="index">
                    <view class="construction_points_item_left">
                        <view class="construction_points_item_from">{{item.order_type}}</view>
                        <view class="construction_points_item_time">{{item.create_time}}</view>
                    </view>
                    <view class="construction_points_item_right" :class="item.value <= 0 ? 'construction_points_item_right_error':''">{{item.value}}</view>
                </view>
                <view v-if="!constructionList[0].pageMore" class="flex justify-center align-center not_more">暂无更多</view>
            </scroll-view>
            <!-- 我的下级列表 -->
            <scroll-view v-if="tabCur == 2 && constructionList[1].list.length > 0" class="things_width_full subordinate_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="flex justify-between align-center things_width_full subordinate_item" v-for="(item,index) in constructionList[1].list" :key="index">
                    <!-- 头像 -->
                    <view class="imageBox">
                        <image class="subordinate_item_cover" :src="item.avatarUrl" />
                    </view>
                    <view class="flex justify-between align-center item_right">
                        <view class="subordinate_item_name">{{item.nickname}}</view>
                        <view class="subordinate_item_profit">{{item.sum_value}}</view>
                    </view>
                </view>
                <!-- 空数据 -->
                <view v-if="!constructionList[1].pageMore" class="flex justify-center align-center not_more">暂无更多</view>
            </scroll-view>
            <view v-if="tabCur == 1 && constructionList[0].list.length <= 0" class="flex justify-center align-center kong_data_box">
                <image class="kong_image" src="./../../../../../static/images/global/kong.png" mode="widthFix" />
                <view class="kong_text">暂无数据</view>
            </view>
            <view v-if="tabCur == 2 && constructionList[1].list.length <= 0" class="flex justify-center align-center kong_data_box">
                <image class="kong_image" src="./../../../../../static/images/global/kong.png" mode="widthFix" />
                <view class="kong_text">暂无数据</view>
            </view>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
import { giftsPointsFraction_api,buildPointsList_api,nextAgentBuildPointsList_api } from '@/api/index.js';
import { appData } from '@/utils/config.js';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import storage from '@/utils/storage.js';
import { pointsType_format } from '@/utils/filters.js';
let that = null;
export default {
    data() {
        return {
            // 导航
            tabCur: 1,
            tabData: [
                {
                    id: 1,
                    name: '积分明细'
                },
                {
                    id: 2,
                    name: '我的下级'
                }
            ],
            // 用户数据
            userData: storage.get("userData") || {},
            userAmount: {},// 积分分数
            constructionList: [
                {
                    page: 1,
                    page_size: 10,
                    list: [],
                    pageMore: true,
                },
                {
                    page: 1,
                    page_size: 10,
                    list: [],
                    pageMore: true
                }
            ],
            buttonType: false,
        }
    },
    watch: {
        '$store.getters.user_userData': function(val) {
            that.userData = val;
        },
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    filters: {
        pointsType_format
    },
    methods: {
        ...mapMutations([
            'modal',
            'user'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         */
        clearPageFun(e) {
            that.constructionList = [
                {
                    page: 1,
                    page_size: 10,
                    list: [],
                    pageMore: true,
                },
                {
                    page: 1,
                    page_size: 10,
                    list: [],
                    pageMore: true
                }
            ];
            that.getFraction();
            that.getConstructionPointsList();
            that.getConstructionAgentsList();
        },
        /**导航栏选择
         * @description:
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        tabSelect(e) {
            if (that.tabCur ===  e.currentTarget.dataset.id) return false;
            that.tabCur = e.currentTarget.dataset.id;
            if (that.tabCur === 1) {
                if (that.constructionList[0].pageMore && that.constructionList[0].list <= 0) {
                    that.getConstructionPointsList();
                }
            }else{
                if (that.constructionList[1].pageMore && that.constructionList[1].list <= 0) {
                    that.getConstructionAgentsList();
                }
            }
        },
        linkToHang(e) {
            uni.navigateTo({
                url: `/pages/things/integral/constructionPoints/hang/index`
            });
        },
        /**获取积分分数
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getFraction(e) {
            let params = {};
            giftsPointsFraction_api(params,that).then(res=>{
                that.userAmount = res.userAmount;
            }).catch(err=>{
                console.log(err);
            })

        },
        /**获取建设分列表
         * @description: 添加数据、判断是否有更多数据
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getConstructionPointsList() {
            let params = {
                page: that.page,
                page_size: that.page_size,
            };
            buildPointsList_api(params,that).then(res=>{
                that.constructionList[0].list = that.constructionList[0].list.concat(res.list);
                if (res.total > that.constructionList[0].list.length) {
                    that.constructionList[0].pageMore = true;
                    that.constructionList[0].page++;
                }else{
                    that.constructionList[0].pageMore = false;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        /**获取建设分下级列表
         * @description: 添加数据、判断是否有更多数据
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getConstructionAgentsList() {
            let params = {
                page: that.constructionList[1].page,
                page_size: that.constructionList[1].page_size,
            };
            nextAgentBuildPointsList_api(params,that).then(res=>{
                that.constructionList[1].list = that.constructionList[1].list.concat(res.list);
                if (res.total > that.constructionList[1].list.length) {
                    that.constructionList[1].pageMore = true;
                    that.constructionList[1].page++;
                }else{
                    that.constructionList[1].pageMore = false;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        /**获取更多数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getMoreData(e) {
            if(that.tabCur == 1) {
                if (that.constructionList[0].pageMore) that.getConstructionPointsList();
            }else{
                if (that.constructionList[1].pageMore) that.getConstructionAgentsList();
            }
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
    },
    onShow: function() {
        that.clearPageFun();
    }
}
</script>

<style lang="scss" scoped>
    @import './index';
</style>